@import url(https://fonts.googleapis.com/css?family=Overpass);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
	box-sizing: border-box;
	font-family: "Overpass", sans-serif;
}

body {
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
  font-family: "Overpass", sans-serif;
}

.tags-input {
	display: flex;
	align-items: flex-start;
	flex-wrap: wrap;
	min-height: 48px;
	width: 480px;
	padding: 0 8px;
	border: 1px solid rgb(214, 216, 218);
	border-radius: 6px;
}

.tags-input:focus-within {
  border: 1px solid #0052cc;
}

.tags-input ul {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 8px 0 0 0;
 
  }

.tags-input ul li{

    width: auto;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    padding: 0 8px;
    font-size: 14px;
    list-style: none;
    border-radius: 6px;
    margin: 0 8px 8px 0;
    background: #0052cc;
}

.tags-input ul li i{

    font-size: 14px;
    margin-left: 8px;
    color: #0052cc;
    border-radius: 50%;
    background: #fff;
    cursor: pointer;
  
}

input {
		border: none;
		height: 46px;
		font-size: 14px;
		padding: 4px 0 0 0;
	
  }
  
input:focus {
  outline: transparent;
}


@media screen and (max-width: 567px) {
	.tags-input {
		width: calc(100vw - 32px);
	}
} 

.DataBase{
  position : fixed;
  top: 20px;
  right : 20px;
  color: black;
}
